<!-- eslint-disable vue/no-use-v-if-with-v-for -->
<template>
  <div>
    <div class="fr page" v-if="totalPages">
      <div class="sui-pagination clearfix">
        <ul>
          <li class="prev disabled">
            <button @click="$emit('getPageNo', pageNo - 1)" :disabled="pageNo == 1">«上一页</button>
          </li>
          <li class="dotted" v-if="starAndEnd.start >= 2"><span>...</span></li>
          <li class="active">
            <button @click="$emit('getPageNo', page)" :class="{ active: page == pageNo }" v-for="(page, index) in End"
              :key="index" v-if="page >= starAndEnd.start">
              {{ page }}
            </button>
          </li>
          <li class="dotted" v-if="pageNo < totalPages - 2"><button>...</button></li>
          <li class="next">
            <button @click="$emit('getPageNo', pageNo + 1)" :disabled="pageNo == totalPages">下一页»</button>
          </li>
        </ul>
        <div><span>共{{ totalPages }}页&nbsp;</span></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MorePage',

  props: ['pageNo', 'total', 'totalPages', 'pageSize', 'continues'],
  data() {
    return {

    };
  },

  computed: {
    // 计算开始页和结束页
    starAndEnd() {
      const { pageNo, totalPages, continues } = this
      // 声明开始和结束页
      let start = 1, end = 1
      if (continues > totalPages) {
        // 连续页码数比总页码数还多
        start = 1
        end = totalPages
      } else {
        // 连续页码数没有总页码数多时
        start = pageNo - parseInt(continues / 2)
        end = pageNo + parseInt(continues / 2)
        // 判断特殊情况
        // 当页码数在前5时
        if (start < 1) {
          start = 1
          end = continues
        }
        if (end > totalPages) {
          start = totalPages - continues + 1
          end = totalPages
        }
      }
      return { start, end }
    },
    // 结束页
    End() {
      if (this.starAndEnd.end < 0) {
        return 1
      } else
        return parseInt(this.starAndEnd.end)
    }
  },

  mounted() {
  },


  methods: {
  },
};
</script>

<style lang="less" scoped>
.page {
  width: 900px;
  height: 66px;
  overflow: hidden;
  float: right;

  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 34px;
    line-height: 34px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: #409eff;
      color: #fff;
    }
  }

  .sui-pagination {
    margin: 18px 0;


    ul {
      margin-left: 0;
      margin-bottom: 0;
      vertical-align: middle;
      width: 510px;
      float: left;

      li {
        line-height: 62px;
        display: inline-block;

        a {
          position: relative;
          float: left;
          line-height: 18px;
          text-decoration: none;
          background-color: #fff;
          border: 1px solid #e0e9ee;
          margin-left: -1px;
          font-size: 14px;
          padding: 9px 18px;
          color: #333;
        }

        &.active {
          a {
            background-color: #fff;
            color: #e1251b;
            border-color: #fff;
            cursor: default;
          }
        }

        &.prev {
          a {
            background-color: #fafafa;
          }
        }

        &[disabled] {
          color: #c0c4cc;
          cursor: not-allowed;
        }

        &.disabled {
          a {
            color: #999;
            cursor: default;
          }
        }

        &.dotted {
          span {
            margin-left: -1px;
            position: relative;
            float: left;
            line-height: 18px;
            text-decoration: none;
            background-color: #fff;
            font-size: 14px;
            border: 0;
            padding: 9px 18px;
            color: #333;
          }
        }

        &.next {
          a {
            background-color: #fafafa;
          }
        }
      }
    }

    div {
      color: #333;
      font-size: 14px;
      float: left;
      width: 241px;
      padding: 10px 0;
    }
  }
}
</style>