// 配置路由信息
// 1. 引入路由组件
import Login from '@/pages/user/login.vue'
import Register from '@/pages/user/register.vue'
import Home from '@/pages/home/home.vue'
import Search from '@/pages/search/search.vue'
import Myorder from '@/pages/Center/myorder/index.vue'

// 2. 对外暴露
export default [
  {
    path: '/home',
    component: Home,
    meta: { isShow: true }
  },
  {
    path: '/',
    redirect: '/home',
    component: Home
  },
  {
    path: '/register',
    component: Register,
    meta: { isShow: false }
  },
  {
    path: '/login',
    component: Login,
    meta: { isShow: false }
  },
  {
    path: '/search/:keyword?',
    name: 'search',
    component: Search,
    meta: { isShow: true }
  },
  {
    path: '/detail/:skuId',
    component: () => import('@/pages/detail/index.vue'),
    meta: { isShow: false }
  },
  {
    path: '/shopcart',
    component: () => import('@/pages/shopCart/index.vue'),
    meta: { isShow: true }
  },
  {
    path: '/addsuccesscart',
    name: 'addsuccesscart',
    component: () => import('@/pages/AddCartSuccess/index.vue'),
    meta: { isShow: true }
  },
  {
    path: '/trade',
    component: () => import('@/pages/Trade/index.vue'),
    meta: { isShow: true }
  },
  {
    path: '/pay',
    component: () => import('@/pages/Pay/index.vue'),
    meta: { isShow: false }
  },
  {
    path: '/paysuccess',
    component: () => import('@/pages/PaySuccess/index.vue'),
    meta: { isShow: false }
  },
  {
    path: '/center',
    component: () => import('@/pages/Center/index.vue'),
    meta: { isShow: false },
    children: [
      {
        path: 'myorder',
        component: Myorder,
      },
      {
        path: '/center',
        redirect: '/center/myorder',
        component: Myorder
      },
      {
        path: 'teamorder',
        component: () => import('@/pages/Center/teamOrder/index.vue'),
        meta: { isShow: false }
      }
    ]
  },

]

