// 购物车的仓库
import { reqGetShopCartList, reqGoshopCart } from '@/api/index'
import { getUUID } from '@/util/token'
const state = {
  // 临时身份
  uuid_token: getUUID(),
  caode: '',
  cartList: {}
}

const mutations = {
  // 添加商品到购物车的成功记录
  GETGOSHOPCART(state, code) {
    state.code = code
  },

  // 获取购物车数据
  GETCARTLIST(state, cartList) {
    state.cartList = cartList
  }
}

const actions = {
  // 添加商品到购物车
  async getGoShopCart({ commit }, { skuId, skuNum }) {
    let result = await reqGoshopCart(skuId, skuNum)
    if (result.code == 200) {
      commit('GETCODE', result.code)
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }

  },

  // 更新购物车数量信息
  async getCartList({ commit }) {
    let result = await reqGetShopCartList()
    if (result.code == 200) {

      commit('GETCARTLIST', result.data)
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }

  },
}

const getters = {
  cartList(state) {
    return state.cartList || []
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}