import Vue from "vue";
import VueRouter from 'vue-router'
// 引入路由组件
import routes from "./routes";
import store from "@/store";

Vue.use(VueRouter)

let router = new VueRouter({
  // 注册路由组件
  routes
})

// 全局守卫：前置守卫（在路由跳转之前进行判断）
router.beforeEach(async (to, from, next) => {
  // to:跳转的目的路由信息
  // from:从哪个路由来的信息
  // next:放行函数。next():直接放行；next('/login'):放行到指定路由
  // 登录了拿到token，没登录的话没有token
  const token = store.state.user.userInfo.token
  // 用户信息
  let name = store.state.user.userInfo.name

  if (token) {
    // 已经登录,不允许再去login
    if (to.path == '/login' || to.path == '/register') {
      next('/')
    } else {
      // 登陆了，但去的不是login。要再验证是否有登录信息
      if (name) {
        next()
      } else {
        // 没有用户信息，要再次派发actions让仓库存储用户信息。再跳转
        try {
          // 获取用户信息成功
          await store.dispatch('loginUserInfo')
          // 放行
          next()
        } catch (error) {
          // token失效了，清除所有信息，并放行到login
          await store.dispatch('removeInfo')
          next('/login')
        }

      }
    }

  } else {
    // 未登录的情况,没有token
    // 不能去：交易相关、购物车、个人中心
    let toPath = to.path
    if (toPath.indexOf('/trade') != -1 || toPath.indexOf('/pay') != -1 || toPath.indexOf('/center') != -1 || toPath.indexOf('/shopcart') != -1) {
      // 把未登录时，想去而未去成的路径。通过query参数传递。登陆后直接跳转至目的路径
      next('/login?redirect=' + toPath)
      next(toPath)
    } else {
      next()
    }

  }
})

// 对外暴露
export default router
