<template>
  <div>
    <GoodsIndex />
    <div class="main">
      <div class="py-container">
        <!--面包屑-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <li class="with-x" v-if="this.searchParams.name">{{ this.searchParams.name }}<i @click="removeName">×</i></li>
            <li class="with-x" v-if="this.searchParams.keyword">{{ this.searchParams.keyword }}<i
                @click="removeKeyword">×</i></li>
            <li class="with-x" v-if="this.searchParams.trademark">{{ this.searchParams.trademark.split(':')[1] }}<i
                @click="removeTradeMark">×</i></li>



          </ul>
        </div>

        <!--selector-->
        <SearchSelector @attrInfo="attrInfo" :attrsList="attrsList" :trademarkList="trademarkList"
          @tradeMark="tradeMark" />

        <!--展示区-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li class="active">
                  <a href="#">综合</a>
                </li>
                <li>
                  <a href="#">销量</a>
                </li>
                <li>
                  <a href="#">新品</a>
                </li>
                <li>
                  <a href="#">评价</a>
                </li>
                <li>
                  <a href="#">价格⬆</a>
                </li>
                <li>
                  <a href="#">价格⬇</a>
                </li>
              </ul>
            </div>
          </div>
          <!-- 商品列表 -->
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="good in goodsList" :key="good.id">
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link :to="`/detail/${good.id}`">
                      <img :src="good.defaultImg" />
                    </router-link>

                  </div>
                  <div class="price">
                    <strong>
                      <em>¥</em>
                      <i>{{ good.price }}</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a target="_blank" href="item.html">{{ good.title }}</a>
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a href="success-cart.html" target="_blank" class="sui-btn btn-bordered btn-danger">加入购物车</a>
                    <a href="javascript:void(0);" class="sui-btn btn-bordered">收藏</a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- 分页 -->
          <MorePage @getPageNo="getPageNo" :continues="5" :pageNo="pageNo" :pageSize="pageSize" :total="total"
            :totalPages="totalPages" />

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from './SearchSelector/SearchSelector'
import MorePage from '@/components/MorePage/index.vue'
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'SearchIndex',

  data() {
    return {
      show: false,
      // 带给服务器的参数
      searchParams: {
        category1Id: "",
        category2Id: "",
        category3Id: "",
        categoryname: "",
        keyword: "",
        // 排序 初始状态是：综合
        order: "1:asc",
        pageNo: 1,
        pageSize: 2,
        // 商品属性
        props: [],
        // 品牌
        trademark: ""
      },
    }
  },

  components: {
    SearchSelector,
    MorePage
  },

  // 在页面挂载完成之前整理参数
  beforeMount() {
    Object.assign(this.searchParams, this.$route.query, this.$route.params)
  },

  mounted() {
    // 调请求
    this.getData()
  },

  computed: {
    // 捞getters数据
    ...mapGetters(['attrsList', 'goodsList', 'trademarkList']),
    // 捞其他数据
    ...mapState({
      pageNo: state => state.search.searchList.pageNo,
      total: state => state.search.searchList.total,
      totalPages: state => state.search.searchList.totalPages,
      pageSize: state => state.search.searchList.pageSize
    })
  },

  methods: {
    // 发请求获取搜索页商品
    getData() {
      this.$store.dispatch('getSearchList', this.searchParams)
    },
    // 选择页码
    getPageNo(pageNo) {
      this.searchParams.pageNo = pageNo
      this.getData()
    },
    // 三级联动面包屑删除
    removeName() {
      const that = this.searchParams
      that.categoryname = undefined
      that.category1Id = undefined
      that.category2Id = undefined
      // this.searchParams.category3Id = ''
      that.category3Id = undefined
      that.name = undefined
      this.getData()

      // 地址栏中的参数也需要修改清空：进行路由的跳转。
      // 本意是删除query参数，所以自己跳自己的时候会把所有参数删除。所以要将params参数传递过去跳转
      if (this.$route.params) {
        this.$router.push({ name: 'search', params: this.$route.params })
      }
    },
    // 关键字面包屑删除
    removeKeyword() {
      const that = this.searchParams
      that.keyword = undefined
      this.getData()
      // 通知兄弟组件【搜索框】清除关键字
      this.$bus.$emit('clear')

      if (this.$route.query) {
        this.$router.push({ name: 'search', query: this.$route.query })
      }
    },
    // 删除品牌区面包屑
    removeTradeMark() {
      this.searchParams.trademark = undefined
      this.getData()
    },
    // 点击品牌区
    tradeMark(trademark) {
      // 整理品牌字段的参数
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`
      // 再次发请求,获取search模块数据进行展示
      this.getData()
    },
    // 点击属性
    attrInfo(attrs, attrsValue) {
      let props = `${attrs.attrId}:${attrsValue}:${attrs.attrName}`

      // 数组去重
      if (this.searchParams.props.indexOf(props) === -1) {
        this.searchParams.props.push(props)
      }
      console.log(attrs, attrsValue);
      this.getData()
    }
  },

  watch: {
    // 监听路由发生变化时再次发请求
    $route() {
      // 整理参数
      Object.assign(this.searchParams, this.$route.query, this.$route.params)
      // 再次发请求
      this.getData()
    }

  }
}
</script>

<style lang="less" scoped>
.main {
  margin: 10px 0;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .bread {
      margin-bottom: 5px;
      overflow: hidden;

      .sui-breadcrumb {
        padding: 3px 15px;
        margin: 0;
        font-weight: 400;
        border-radius: 3px;
        float: left;

        li {
          display: inline-block;
          line-height: 18px;

          a {
            color: #666;
            text-decoration: none;

            &:hover {
              color: #4cb9fc;
            }
          }
        }
      }

      .sui-tag {
        margin-top: -5px;
        list-style: none;
        font-size: 0;
        line-height: 0;
        padding: 5px 0 0;
        margin-bottom: 18px;
        float: left;

        .with-x {
          font-size: 12px;
          margin: 0 5px 5px 0;
          display: inline-block;
          overflow: hidden;
          color: #000;
          background: #f7f7f7;
          padding: 0 7px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #dedede;
          white-space: nowrap;
          transition: color 400ms;
          cursor: pointer;

          i {
            margin-left: 10px;
            cursor: pointer;
            font: 400 14px tahoma;
            display: inline-block;
            height: 100%;
            vertical-align: middle;
          }

          &:hover {
            color: #28a3ef;
          }
        }
      }
    }

    .details {
      margin-bottom: 5px;

      .sui-navbar {
        overflow: visible;
        margin-bottom: 0;

        .filter {
          min-height: 40px;
          padding-right: 20px;
          background: #fbfbfb;
          border: 1px solid #e2e2e2;
          padding-left: 0;
          border-radius: 0;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);

          .sui-nav {
            position: relative;
            left: 0;
            display: block;
            float: left;
            margin: 0 10px 0 0;

            li {
              float: left;
              line-height: 18px;

              a {
                display: block;
                cursor: pointer;
                padding: 11px 15px;
                color: #777;
                text-decoration: none;
              }

              &.active {
                a {
                  background: #e1251b;
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .goods-list {
        margin: 20px 0;

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            height: 100%;
            width: 20%;
            margin-top: 10px;
            line-height: 28px;

            .list-wrap {
              .p-img {
                padding-left: 15px;
                width: 215px;
                height: 255px;

                a {
                  color: #666;

                  img {
                    max-width: 100%;
                    height: auto;
                    vertical-align: middle;
                  }
                }
              }

              .price {
                padding-left: 15px;
                font-size: 18px;
                color: #c81623;

                strong {
                  font-weight: 700;

                  i {
                    margin-left: -5px;
                  }
                }
              }

              .attr {
                padding-left: 15px;
                width: 85%;
                overflow: hidden;
                margin-bottom: 8px;
                min-height: 38px;
                cursor: pointer;
                line-height: 1.8;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;

                a {
                  color: #333;
                  text-decoration: none;
                }
              }

              .commit {
                padding-left: 15px;
                height: 22px;
                font-size: 13px;
                color: #a7a7a7;

                span {
                  font-weight: 700;
                  color: #646fb0;
                }
              }

              .operate {
                padding: 12px 15px;

                .sui-btn {
                  display: inline-block;
                  padding: 2px 14px;
                  box-sizing: border-box;
                  margin-bottom: 0;
                  font-size: 12px;
                  line-height: 18px;
                  text-align: center;
                  vertical-align: middle;
                  cursor: pointer;
                  border-radius: 0;
                  background-color: transparent;
                  margin-right: 15px;
                }

                .btn-bordered {
                  min-width: 85px;
                  background-color: transparent;
                  border: 1px solid #8c8c8c;
                  color: #8c8c8c;

                  &:hover {
                    border: 1px solid #666;
                    color: #fff !important;
                    background-color: #666;
                    text-decoration: none;
                  }
                }

                .btn-danger {
                  border: 1px solid #e1251b;
                  color: #e1251b;

                  &:hover {
                    border: 1px solid #e1251b;
                    background-color: #e1251b;
                    color: white !important;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
      }

      .page {
        width: 733px;
        height: 66px;
        overflow: hidden;
        float: right;

        .sui-pagination {
          margin: 18px 0;

          ul {
            margin-left: 0;
            margin-bottom: 0;
            vertical-align: middle;
            width: 490px;
            float: left;

            li {
              line-height: 18px;
              display: inline-block;

              a {
                position: relative;
                float: left;
                line-height: 18px;
                text-decoration: none;
                background-color: #fff;
                border: 1px solid #e0e9ee;
                margin-left: -1px;
                font-size: 14px;
                padding: 9px 18px;
                color: #333;
              }

              &.active {
                a {
                  background-color: #fff;
                  color: #e1251b;
                  border-color: #fff;
                  cursor: default;
                }
              }

              &.prev {
                a {
                  background-color: #fafafa;
                }
              }

              &.disabled {
                a {
                  color: #999;
                  cursor: default;
                }
              }

              &.dotted {
                span {
                  margin-left: -1px;
                  position: relative;
                  float: left;
                  line-height: 18px;
                  text-decoration: none;
                  background-color: #fff;
                  font-size: 14px;
                  border: 0;
                  padding: 9px 18px;
                  color: #333;
                }
              }

              &.next {
                a {
                  background-color: #fafafa;
                }
              }
            }
          }

          div {
            color: #333;
            font-size: 14px;
            float: right;
            width: 241px;
          }
        }
      }
    }
  }
}
</style>