// 导入requests
import requests from "./requests";
// 导入Mock请求
import Mock from "./mockRequest";

// 获取验证码
export const reqGetCode = (phone) => requests({
  url: `/user/passport/sendCode/${phone}`,
  method: 'GET'
})

// 注册请求
export const reqRegisterUser = (data) => requests({
  url: '/user/passport/register',
  method: 'POST',
  data
})

// 登录请求
export const reqLoginUser = (data) => requests({
  url: `/user/passport/login`,
  method: 'POST',
  data
})

// 获取轮播图请求
export const reqGetBannerList = () => Mock.get('/banner')

// 获取楼层请求
export const reqFloorList = () => Mock.get('/floor')

// 获取用户地址
export const reqGetAddress = () => Mock.get('/address')

// 获取三级联动
export const reqGetTayNavList = () => requests.get('/product/getBaseCategoryList')

// 搜索请求
export const reqSearchList = (params) => requests({
  url: '/list',
  method: 'POST',
  data: params
})

// 获取商品详情信息
export const reqGetDetail = (skuId) => requests.get(`/item/${skuId}`)

// 将商品添加至购物车
export const reqGoshopCart = (skuId, skuNum) => requests.post(`/cart/addToCart/${skuId}/${skuNum}`)

// 获取购物车商品列表数据
export const reqGetShopCartList = () => requests({
  url: '/cart/cartList',
  method: 'GET'
})

// 删除商品
export const reqDeleteCart = (skuId) => requests({
  url: `/cart/deleteCart/${skuId}`,
  method: 'DELETE'
})

// 获取订单交易页信息
export const reqGetTradeInfo = () => requests({
  url: '/order/auth/trade',
  method: 'GET'
})

// 获取用户地址
export const reqGetUserAddress = () => requests({
  url: '/user/userAddress/auth/findUserAddressList',
  method: 'GET'
})

// 提交订单
export const reqPostOrderInfo = (tradeNo, data) => requests({
  url: `/order/auth/submitOrder?tradeNo=${tradeNo}`,
  method: 'POST',
  data
})

// 获取订单支付信息
export const reqPayInfo = (orderId) => requests({
  url: `/payment/weixin/createNative/${orderId}`,
  method: 'GET'
})

// 获取支付状态
export const reqPayStatus = (orderId) => requests({
  url: `/payment/weixin/queryPayStatus/${orderId}`,
  method: 'GET'
})

// 获取我的订单
export const reqGetMyOrder = (page, limit) => requests({
  url: `/order/auth/${page}/${limit}`,
  method: 'GET'
})

// 切换商品选中状态
export const reqChangeCheck = (skuID, isChecked) => requests({
  url: `/cart/checkCart/${skuID}/${isChecked}`,
  method: 'GET'
})