import axios from "axios";

// 创建mock请求
const Mock = axios.create({
  baseURL: '/mock',
  timeout: 5000
})

// 请求拦截器
Mock.interceptors.request.use((config) => {
  return config
})

// 响应拦截器
Mock.interceptors.response.use((res) => {
  // 成功的回调函数：服务器响应数据回来之后，响应拦截器可以检测到。做一些事
  // 进度条结束
  return res.data
}, (error) => {
  // 响应失败的回调函数
  console.log(error);
})

// 对外暴露
export default Mock



