<template>
  <div>
    <!-- 头部 -->
    <header class="header">
      <!-- 头部的第一行 -->
      <div class="top">
        <div class="container">
          <div class="loginList">
            <p>尚品汇欢迎您！</p>
            <p v-if="getname">
              <a>{{ getname }}</a>
              <a class="register" @click="logout">退出登录</a>
            </p>
            <p v-else-if="name">
              <a>{{ name }}</a>
              <a class="register" @click="logout">退出登录</a>
            </p>
            <p v-else>
              <span>请</span>
              <router-link to="/login">登录</router-link>
              <router-link to="/register" class="register">免费注册</router-link>
            </p>

          </div>
          <div class="typeList">
            <router-link to="/center">我的订单</router-link>
            <router-link to="/shopcart">我的购物车</router-link>
            <a href="###">我的尚品汇</a>
            <a href="###">尚品汇会员</a>
            <a href="###">企业采购</a>
            <a href="###">关注尚品汇</a>
            <a href="###">合作招商</a>
            <a href="###">商家后台</a>
          </div>
        </div>
      </div>
      <!--头部第二行 搜索区域-->
      <div class="bottom">
        <h1 class="logoArea">
          <router-link to="/" class="logo" title="尚品汇">
            <img src="./images/logo.png" alt="">
          </router-link>

        </h1>
        <div class="searchArea">
          <form action="###" class="searchForm">
            <input v-model="keyword" type="text" class="input-error input-xxlarge" />
            <button class="sui-btn btn-xlarge btn-danger" type="button" @click="goSearch">搜索</button>
          </form>
        </div>
      </div>
    </header>
  </div>
</template>

<script>
import { getName, getToken, removeToken } from '@/util/token'

export default {
  name: 'HeaderIndex',

  data() {
    return {
      name: '',
      token: '',
      keyword: ''
    };
  },
  mounted() {
    this.getName()
    this.getToken()
    this.$bus.$on('clear', () => {
      this.keyword = ''
    })
  },
  computed: {
    getname() {
      return this.$store.state.user.userInfo.name
    }
  },

  methods: {
    // 获取用户名
    getName() {
      this.name = getName()
    },
    // 获取token
    getToken() {
      this.token = getToken()
    },
    // 退出登录
    logout() {
      this.name = ''
      this.token = ''
      this.$store.dispatch('removeInfo')
      removeToken()
      this.$router.push('/')
    },

    // 去搜索
    goSearch() {
      if (this.$route.query) {
        let location = {
          name: "search",
          params: { keyword: this.keyword || undefined },
        };
        location.query = this.$route.query;
        this.$router.push(location);
      }
      this.keyword = ''
    },


  },
};
</script>

<style lang="less" scoped>
.header {
  &>.top {
    background-color: #eaeaea;
    height: 30px;
    line-height: 30px;

    .container {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .loginList {
        float: left;

        p {
          float: left;
          margin-right: 10px;

          .register {
            border-left: 1px solid #b3aeae;
            padding: 0 5px;
            margin-left: 5px;
          }
        }
      }

      .typeList {
        float: right;

        a {
          padding: 0 10px;

          &+a {
            border-left: 1px solid #b3aeae;
          }
        }

      }

    }
  }

  &>.bottom {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;

    .logoArea {
      float: left;

      .logo {
        img {
          width: 175px;
          margin: 25px 45px;
        }
      }
    }

    .searchArea {
      float: right;
      margin-top: 35px;

      .searchForm {
        overflow: hidden;

        input {
          box-sizing: border-box;
          width: 490px;
          height: 32px;
          padding: 0px 4px;
          border: 2px solid #ea4a36;
          float: left;

          &:focus {
            outline: none;
          }
        }

        button {
          height: 32px;
          width: 68px;
          background-color: #ea4a36;
          border: none;
          color: #fff;
          float: left;
          cursor: pointer;

          &:focus {
            outline: none;
          }
        }
      }
    }
  }
}
</style>